$text_color : #444444;
$border_color: #e3e3e3;
$green: #27ae60;
$gray: #666666;
$light_gray: #999999;
$blue: #19a2d8;
$light_blue: #4dbce9;
$link_color: #108dbf;
$very_light_blue: #eaf3f7;

@mixin widthToPricentByPixelOn1200($pixel){
    width: $pixel * 100% / 1200;
}
@mixin transition($transition...) {
    -moz-transition:    $transition;
    -o-transition:      $transition;
    -webkit-transition: $transition;
    transition:         $transition;
}

body{
    font-family: 'Roboto', sans-serif;
    color: $text_color;
    font-size: 14px;
}
a{
    color: $link_color;
    &:hover{
        color: $link_color;
    }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
@media (min-width: 1230px) {
  .container {
    width: 1200px;
  }
}
.js-link{
    position: relative;
    &:after{
        content: '';
        display: block;
        left: 0;
        right: 0;
        bottom: 0;
        border-top: 1px dotted $text_color;

    }
}
header.line{
    border-bottom: 1px solid  $border_color;
    min-height: 49px;
    line-height: 45px;
    font-size: 14px;
    padding-top: 3px;
    a{
        color: $text_color;
    }
    .js-link{
        &:after{
            margin-top: -15px;
        }
    }
    .icon{
        margin-right: 9px;
        &.geo{
            margin-top: 15px;
        }
        &.time{
            margin-top: 15px;
        }
        &.email{
            margin-top: 17px;
        }
        &.phone{
            margin-top: 16px;
            @media (max-width: 1199px) {
                margin-top: 4px;
            }
        }
    }
    .row{
        @media (min-width: 992px) {
            >.col1{
                @include widthToPricentByPixelOn1200(236);
            }
            >.col2{
                @include widthToPricentByPixelOn1200(289);
            }
            >.col3{
                @include widthToPricentByPixelOn1200(304);
            }
            >.col4{
                @include widthToPricentByPixelOn1200(371);
            }
        }
    }
    .phones{
        @media (max-width: 1199px) {
            line-height: 20px;
        }
        .phone{
            margin-left: 0px;
            font-size: 18px;
            font-weight: bold;
            &:first-child{
                margin-left: 16px;
            }
        }
    }
}
header.header{
    background: #e1ebee url(../images/bgh.png) 0 0 repeat;
    min-height: 157px;
    border-bottom: 1px solid  $border_color;
    padding-top: 21px;
    .logo{
        display: block;
    }
    .row{
        @media (min-width: 992px) {
            >.col1{
                @include widthToPricentByPixelOn1200(236);
            }
            >.col2{
                @include widthToPricentByPixelOn1200(765);
            }
            >.col3{
                @include widthToPricentByPixelOn1200(199);
            }
        }
        &.menu-items{
            @media (min-width: 992px) {
                >.col1{
                    @include widthToPricentByPixelOn1200(251);
                }
                >.col2{
                    @include widthToPricentByPixelOn1200(621);
                    background-color: $blue;
                }
                >.col3{
                    @include widthToPricentByPixelOn1200(149);
                }
                >.col4{
                    @include widthToPricentByPixelOn1200(179);
                }
            }
        }
    }
    .form-group-search{
        margin-top: 8px;
        position: relative;
        @media (min-width: 992px) {
            margin-left: -1px;
            margin-bottom: 0;
        }
        .submit_button{
            position: absolute;
            top: 0;
            right: 0;
        }
        .form-control{
            padding-right: 100px;
        }
        .btn.btn-default{
            background: none;
            background-color: $very_light_blue;
            border-radius:  0 2px 2px 0;
            padding: 6px 29px;
        }
    }
    .btn-collMeWrap{
        margin-top: 8px;
    }
    .menu-items{
        margin-top: 22px;
    }
    .menu-item{
        display: block;
        line-height: 30px;
        padding: 17px 0 14px 0;
        background-color: $blue;
        font-size: 14px;
        color: #ffffff;
        font-weight: bold;
        margin-bottom: -1px;
        text-transform: uppercase;
        text-align: center;
        position: relative;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        outline: none;
        &:active:focus,
        &:active,
        &:focus,
        &:hover{
            text-decoration: none;
            background-color: #42b5e3;
            outline: none;
        }
        &.catalog-menu-item{
            background-color: $green;
            padding-left: 46px;
            padding-right: 36px;
            @media  (max-width: 1199px) and (min-width: 992px) {
                padding-left: 27px;
                padding-right: 23px;
            }
            .icon{
                position: absolute;
                &.catalog{
                    top: 26px;
                    left: 29px;
                    @media  (max-width: 1199px) and (min-width: 992px) {
                        left: 8px;
                    }
                }
                &.down{
                    top: 28px;
                    right: 27px;
                    @media  (max-width: 1199px) and (min-width: 992px) {
                        right: 8px;
                    }
                }
            }
        }
        &.special-menu-item{
            background-color: #108dbf;
            line-height: 14px;
            text-align: left;
            padding-left: 55px;
            padding-right: 5px;
            @media  (max-width: 1199px) and (min-width: 992px) {
                padding-left: 34px;
            }
            &.special-menu-item-cart{
                padding-left: 72px;
                @media  (max-width: 1199px) and (min-width: 992px) {
                    padding-left: 38px;
                }
                &:after{
                    content: '';
                    display: block;
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    background-color: $blue;
                    width: 1px;
                }
            }
            .icon{
                position: absolute;
                &.personal{
                    top: 17px;
                    left: 23px;
                    @media  (max-width: 1199px) and (min-width: 992px) {
                        left: 6px;
                    }
                }
                &.cart{
                    top: 22px;
                    left: 21px;
                    @media  (max-width: 1199px) and (min-width: 992px) {
                        left: 6px;
                    }
                }
            }
            .small{
                display: block;
                text-transform: none;
                font-size: 12px;
                color: #b0e5fa;
                line-height: 16px;
                font-weight: normal;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            .value{
                position: absolute;
                top: 12px;
                left: 38px;
                min-height: 24px;
                width: 24px;
                background-color: $green;
                line-height: 24px;
                text-align: center;
                color: #ffffff;
                font-size: 12px;
                font-weight: normal;
                border-radius: 50%;
                @media  (max-width: 1199px) and (min-width: 992px) {
                    left: 14px;
                    top: 6px;
                }

            }
        }
    }
}
.form-control{
    border-radius: 2px;
    font-size: 13px;
    line-height: 36px;
    height: 38px;
    padding-left: 22px;

}
.btn{
    font-size: 13px;
    line-height: 24px;


}
