        border-color: $green;
    }
}
.btn-gray{
    background: none;
    background-color: #91a2ac;
    border-color: #91a2ac;
    color: #ffffff;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
    &:active:focus,
    &.active,
    &:active,
    &:hover,
    &:focus{
        background-color: #91a2ac;
        border-color: #91a2ac;
