    }
}
.btn-sort{
    background: none;
    background-color: #ffffff;
    border-color: $border_color;
    color: $text_color;
    -webkit-box-shadow: none;
    box-shadow: none;
    text-shadow: none;
    &:active:focus,
    &.active,
    &:active,
    &:hover,
    &:focus{
        background-color: #ffffff;
        border-color: $border_color;
        color: $text_color;
    }
    &.active-sort{
        background-color: #efeedb;
        &:active:focus,
        &.active,
        &:active,
        &:hover,
        &:focus{
            background-color: #efeedb;
        }
    }
}
.btn{
    .icon{
        margin-right: 9px;
        &.user{
            margin-top: 4px;
        }
        &.catalog{
            margin-top: 7px;
        }
    }
}
.no-padding-left{
    padding-left: 0 !important;
}
.no-padding-right{
    padding-right: 0 !important;
}
@media (min-width: 992px) {
    .no-padding-md{
        padding-right: 0 !important;
        padding-left: 0 !important;
    }
    .no-padding-right-md{
        padding-right: 0 !important;
    }
    .no-padding-left-md{
        padding-left: 0 !important;
    }

}
@media (max-width: 991px) and (min-width: 768px) {
    .no-padding-sm{
        padding-right: 0 !important;
        padding-left: 0 !important;
    }
    .no-padding-right-sm{
        padding-right: 0 !important;
    }
    .no-padding-left-sm{
        padding-left: 0 !important;
    }

}
@media (max-width: 767px){
    .no-padding-right-xs{
        padding-right: 0 !important;
    }
    .no-padding-left-xs{
        padding-left: 0 !important;
    }
}
ul.site-menu-imtems{
    display: table;
    margin: 0;
    width: 100%;
    @media (max-width: 767px){
        display: block;
    }
    li{
        display: table-cell;
        @media (max-width: 767px){
            display: block;
        }
        .menu-item{
            position: relative;
            @media (max-width: 767px){
                border-top: 1px solid #42b5e3;
            }
            @media (min-width: 768px){
                &:after{
                    position: absolute;
                    content: '';
                    top: 0;
                    bottom: 0;
                    left: 0;
                    width: 1px;
                    background-color: #42b5e3;
                }
            }

        }
        &:first-child{
            .menu-item{
                padding-left: 5px;
                padding-right: 5px;
                &:after{
                    background-color: transparent;
                    @media (max-width: 767px){
                        border-top: none;
                    }
                }
            }
        }
    }
}
.main-container{
    @media (max-width: 991px){
        overflow: hidden;
    }
    @media (min-width: 768px){
        min-height: 500px;
    }
}
footer.footer{
