    border-top: 1px solid  $border_color;
    padding: 44px 0 43px 0;
    line-height: 24px;
    font-size: 13px;
    color: $gray;
    p{
        margin-bottom: 10px;
    }
    .row{
        @media (min-width: 992px) {
            >.col1{
                @include widthToPricentByPixelOn1200(285);
            }
            >.col2{
                @include widthToPricentByPixelOn1200(230);
            }
            >.col3{
                @include widthToPricentByPixelOn1200(401);
            }
            >.col4{
                @include widthToPricentByPixelOn1200(284);
            }
        }
    }
    a{
        color: $gray;
        &:hover{
            color: $blue;
            text-decoration: none;
        }
    }
    .logo-footer{
        margin-top: 6px;
    }
    .copyright{
        color: $light_gray;
        line-height: 1.2;
        margin-top: 50px;
    }
    .icon{
        &.phone{
            margin-top: 6px;
            margin-right: 9px;
        }
