        }
        &.time{
            margin-top: 3px;
            margin-right: 9px;
        }

    }
    .social-links{
        margin-top: 20px;
        a{
            display: block;
            width: 33px;
            height: 34px;
            border-bottom: 1px solid #ffffff;
            background-color: #bac8cc;
            border-radius: 2px;
            margin-left: 7px;
            &:hover{
                background-color: #29ceff;
            }
            &:first-child{
                margin-left: 0;
            }
            .icon{
                &.tw{
                    margin-top: 11px;
                    margin-left: 10px;
                }
                &.fb{
                    margin-top: 11px;
                    margin-left: 13px;
                }
                &.vk{
                    margin-top: 12px;
                    margin-left: 13px;
                }
                &.ok{
                    margin-top: 11px;
                    margin-left: 12px;
                }
                &.g-plus{
                    margin-top: 11px;
                    margin-left: 10px;
                }
            }
        }
    }
}
.main-page-row{

    @media (min-width: 992px) {
        margin-left: -17px;
        margin-right: -17px;
        >.col1{
            @include widthToPricentByPixelOn1200(269);
            padding-left: 17px;
            padding-right: 17px;
        }
        >.col2{
            @include widthToPricentByPixelOn1200(931);
            padding-left: 17px;
            padding-right: 17px;
            padding-top: 35px;
            position: relative;
            .sub-catalog{
                position: absolute;
                left: -17px;
                right: 17px;
                top: 0;
            }
        }
    }


}
@media (min-width: 992px) {
    .navbar-nav{
        float: none;
    }
}
.navbar-default{
    &.catalog-navbar{
        background: none;
        border-radius: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
        border: 1px solid $border_color;
        .navbar-header{
            background-color: #27ae60;
            color: #ffffff;
            .navbar-brand{
                color: #ffffff;
                font-size: 14px;
                text-transform: uppercase;
                font-weight: bold;
                text-shadow: none;
                padding: 20px 15px;
                height: 60px;
            }
            .icon.catalog{
                margin-top: 25px;
                margin-left: 15px;
                @media (min-width: 992px) {
                    display: none;
                }
            }
        }
        .navbar-toggle{
            border-color: #ffffff;
            margin-top: 14px;
            margin-bottom: 13px;
            .icon-bar{
                background-color: #ffffff;
            }
            &:hover,
            &:focus{
                background-color: transparent;
            }

        }
        .navbar-nav{
            > li{
                float: none;
                border-top:  1px solid $border_color;
                &:first-child{
                    border-top: none;
                }
                a{
                    color: $text_color;
                    font-size: 14px;
                    padding-top: 14px;
                    padding-bottom: 14px;
                    padding-left: 20px;
                    .cnt{
                        color: $light_gray;
                    }
                }
                &:hover,
                &.active{
                    > a {
                        background: none;
                        -webkit-box-shadow: none;
                        box-shadow: none;
                        background-color: $very_light_blue;
                    }
                }
                .dropdown-menu{
                    @media (min-width: 992px) {
                        display: none !important;
                    }
                }
            }
            > .open{
                > a{
                    background: none;
                    background-color: $very_light_blue;
                    -webkit-box-shadow: none;
                    box-shadow: none;
                }
            }
        }
        .navbar-collapse{
            @media (min-width: 768px){
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
    .container-fluid{
        @media (min-width: 768px){
            padding-left: 0;
            padding-right: 0;
        }
    }
}
.special-img-wrap{
    display: block;
    position: relative;
    margin-bottom: 30px;
    &:hover{
        text-decoration: none;
    }
    .special{
        display: block;
        font-size: 14px;
        font-weight: bold;
        color: $text_color;
        border-left: 3px solid $green;
        background-color: $very_light_blue;
        line-height: 1.2;
        padding: 19px 15px 19px 25px;
        margin: 0;
        text-transform: uppercase;
        &:hover{
            background-color: $green;
            color: #ffffff;
        }
    }
    .price{
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 18px;
        line-height: 1.2;
        background-color: $blue;
        color: #ffffff;
        padding: 17px 32px 16px 32px;
        font-weight: bold;
    }
    .price-small{
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        font-size: 13px;
        line-height: 1.2;
        font-weight: normal;
        background-color: $blue;
        color: #ffffff;
        padding: 13px 18px 12px 18px;
    }
}
.detail-in-link{
    display: block;
    font-size: 14px;
    line-height: 1.5;
    color: $text_color;
    text-decoration: none;
    a:hover &{
        color: $link_color;
        text-decoration: none;
    }
}
a.main-news-item{
    display: block;
    margin-bottom: 30px;
    text-decoration: none;
    .special-img-wrap{
        margin-bottom: 20px;
    }
}
h3,
.h3{
    font-weight: bold;
    font-size: 18px;
    margin-top: 0;
    margin-bottom: 18px;
}
.margin-bottom-43{
    margin-bottom: 43px;
}
p{
    margin-bottom: 24px;
}
@media (min-width: 992px) {
    .row.row-m-17{
        margin-left: -17px;
        margin-right: -17px;
        > div{
            padding-left: 17px;
            padding-right: 17px;
        }
    }

}
.h3.special,
h3.special,
.h2.special,
h2.special{
    position: relative;
    margin: 0 0 29px 0;
    font-size: 20px;
    line-height: 1.2;
    font-weight: bold;
    text-transform: uppercase;
    .title{
        position: relative;
        background-color: #ffffff;
        padding-right: 7px;
    }
    &:before{
        content: '';
        position: absolute;
        display: block;
        background-color: $border_color;
        top: 50%;
        left: 0;
        right: 0;
        height: 1px;
    }
}
h1.border-top,
.h1.border-top{
    font-size: 30px;
    color: $text_color;
    font-weight: bold;
    margin-top: 7px;
    line-height: 1.2;
    padding-top: 19px;
    margin-bottom: 19px;
    border-top: 1px solid $border_color;
}
h1.h2.special{
    margin-bottom: 25px;
}
.h3.special,
h3.special{
    font-size: 16px;
    .title{
        color: $text_color;
    }
    .icon{
        display: inline-block;
        top: 3px;
        position: relative;
        margin-left: 5px;
    }
}
section.reviews{
    .review-item{
        a{
            display: block;
            position: relative;
            .hover{
                display: none;
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: rgba(39, 174, 96, 0.5);
                .icon.big-zoom{
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    margin-top: -41px;
                    margin-left: -41px;
                }
            }
            &:hover{
                .hover{
                    display: block;
                }
            }
        }
        img{
            display: block;
            border: 1px solid $border_color;
        }
    }

}
.owl-carousel{
    margin-bottom: 39px;
    .owl-nav{
        position: absolute;
        top: -57px;
        right: 0;
        padding: 0;
        margin: 0;
        padding-left: 6px;
        background-color: #ffffff;
        .owl-next,
        .owl-prev{
            display: inline-block;
            background-color: $blue;
            width: 35px;
            height: 35px;
            border-radius: 0;
            margin: 0;
            padding: 13px 14px 13px 14px;
            &:hover{
                background-color: $light_blue;
            }
            &.disabled:hover{
                background-color: $blue;
            }
        }
        .owl-prev{
            margin-right: 1px;
        }




    }
}
section.audio-reviews{
    audio{
        width: 100%;
    }
}
section.main-about{
    margin-bottom: 19px;
    h2{
        margin-top: 0;
        font-weight: normal;
        font-size: 24px;
        color: $text_color;
    }
}
.audio-track-wrap{
    padding: 11px 22px 11px 23px;
    margin-bottom: 10px;
    background-color: $very_light_blue;
    line-height: 1;
    font-size: 14px;
    audio{
        display: none;
    }
