.nav-tabs.card_tab > li.active > a,
.nav-tabs.card_tab > li.active > a:hover,
.nav-tabs.card_tab > li.active > a:focus {
    color: inherit;
    cursor: default;
    background-color: #fff;
    border: none;
}
.nav-tabs.card_tab > li > a {
  border-radius: 0px;}


.star {
  width: 20px;
  height: 18px;
  background-position: 0 0;
  float: left;
}
.card-icons {
  background: url(../images/icons/star.png) no-repeat;
}

.danger-block {
  border: 2px solid #d04242;
  position: relative;
  overflow: hidden;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-right: 35px;
  margin-right: 30px;
  p {
    margin:0;
  }

  &_text {
    padding-left: 80px;
    color: $gray;
  }

  &_title {
    width: 145px;
    top: 20px;;
    left: -50px;
    padding: 1em;
    transform: rotate(-90deg) translate(0, 50%);
    transform-origin:  50% 100%;
    position: absolute;
    background-color: #d04242;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 0.2em;
  }
}



//slider
.owl-card .item img {
    display: block;
    width: 100%;
    height: auto;
}
